// 引入
@import "./_include/_include.sass";

* {
  vertical-align: middle;
  font-family: 'Microsoft JhengHei'; }

h1,h2,h3,h4,h5,h6,p {
  margin: 0; }

ul {
  margin: 0;
  padding: 0;
  li {
    list-style-type: none; } }

[data-bg] {
  display: block;
  @include size(100%,auto);
  background-size: cover;
  background-repeat: no-repeat; }

.flex-row {
  display: flex;
  flex-wrap: wrap; }



.wrapper {
  padding: 30px 0; }
