//color
$color_main: #3d5c98;
$color_hover: #0064b9;
$color_hover_bg: #3d5c98;
$color_text: #30323d;
$color_sub: #c3c3c3;
$color_title: #333333;
$color_btn: #f3b12c;
$color_border: #414141;


//mobile
$min1500: "screen and (min-width: 1500px)";
$min1300: "screen and (min-width: 1300px)";
$max1200: "screen and (max-width: 1200px)";
$max1000: "screen and (max-width: 1000px)";
$max640: "screen and (max-width: 640px)";
$max500: "screen and (max-width: 500px)";


//mixin
@mixin size($w,$h:$w) {
  width: $w;
  height: $h; }

@mixin clearfix {
  &:after {
    content: "";
    display: table;
    clear: both; } }

@mixin t($t:0.5s) {
  transition-duration: $t; }

@mixin cr($c:#222) {
  color: $c; }

@mixin border($w:1px,$s:solid,$c:#222) {
  border: $w $s $c; }

@mixin circle($r) {
  width: $r;
  height: $r;
  border-radius: 50%; }

@mixin flex-default {
  display: flex;
  flex-wrap: wrap; }

@mixin line($s:solid,$w:1px,$c:#e5e5e5) {
  border: $s $w $c; }
